@use 'base/mixins' as m;
@use 'base/variables' as v;

.barra {
    display: none;

    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__navegacion {
        @include m.tablet {
            display: flex;
            gap: 4rem;
        }
    }

    &__link {
        color: v.$gris;
    }

    &__linkk {
        color: v.$blanco;
    }

    &__nombre {
        color: v.$blanco;
        margin: 0;
    }
}
